.popular{
    display:flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    height: 90vh;
    background: linear-gradient(180deg,  #fde1ff,  #e1ffea22 60% );
}

.popular h1{
    color:#171717;
    font-size:50px;
    font-weight: 600;
}
.popular hr{
    width: 300px;
    height: 6px;
    border-radius: 10px;
    background: #252525;

}
.popular-item{
    margin-top: 50px;
    display: flex;
    font-size: 40px;
    gap: 100px;
}

.popular-item img{
    max-width: 100%;
    height: 100%;
}

