.descriptionbox{
    margin: 120px 170px;
   }
   .descriptionbox-navigator{
       display: flex;
       
   }
   .descriptionbox-nav-box{
       display: flex;
       align-items: center;
       justify-content: center;
       font-size: 20px;
       font-weight: 600;
       width: 171px;
       height: 70px;
       border: 5px solid #a484e3;
   }
   .descriptionbox-nav-box.fade{
       background: #FBFBFB;
       color: #555;
   }
   .descriptionbox-description{
       display: flex;
       flex-direction: column;
       gap: 25px;
       border: 5px solid #af84e3;
       padding: 48px;
       padding-bottom: 70px;
       font-size: 22px;
       /* background-color:#fbe2f8 ; */
   }